
.container{
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  align-items: center;
  margin-top: 1.5em;
  color: #2E3840;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.title{
  display: flex;
  justify-content: center;
  
}
.titulo-e-data{
  font-size: 2rem;
  border-bottom: white 1px solid;
}
.title-informacoes{
  background-color: #2E3840;
  padding: 20px;
  color: white; 
  font-size: 1.4em;
  border-radius: 0px 20px 20px 0px;
  
}
.more-informations{
  margin-top: 2em;
}
.more-informations div{
  margin-top: 30px;
  margin-bottom: 20px;
}
.title img{
  border-radius: 20px 0px 0px 20px;
}

.plot{
  text-align: center;
  margin-top: 2em;
  margin-bottom: 2em;
}
.plot-title h1{
  font-size: 4em;
}
.plot-text p{
  font-size: 2.5em;
}
.awards{
  text-align: center;
    margin-top: 2em;
    margin-bottom: 2em;
}
.awards-title h1{
  font-size: 4em;
}
.awards-text p{
  font-size: 2.5em;
}

.ratings{
  text-align: center;
  margin-top: 2em;
  margin-bottom: 2em;
}
.ratings-title h1{
  font-size: 4em;
}
.ratings-text p{
  font-size: 2.5em;
}
.ratings-notes{
  display: flex;
  justify-content: space-around;
  font-size: 2em;
}
.color-81-100{
  color: #66cc33;
}
.color-61-80{
  color: #66cc33
}
.color-40-60{
  color: #ffcc33;
}
.color-20-39{
  color: #ff0000;
}
.color-0-19{
  color: #ff0000;
}
@media (max-width:840px) {
  .titulo-e-data{
    border-bottom: none;
  }
  .container h1{
    font-size: 1.4em;
  }
  .plot h1, .ratings h1, .awards h1{
    font-size: 3em;
  }
  .title{
    display: block;
    margin-top: -25px;
    text-align: center;
    background-color: #2E3840;
  }
  .plot-text p, .awards-text p{
    font-size: 1.6em;
  }
  .ratings-notes{
    display: block;
    justify-content: space-around;
    font-size: 2em;
  }
  .ratings-notes h2{
    font-size: 1em;
  }
}



